@import url('https://fonts.googleapis.com/css2?family=Creepster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;1,700;1,900&display=swap');

.formContainer {
    position: absolute;
    right: 100px;
    top: 150px;
    width: 26%;
    height: 70%;
    background-color: rgb(36, 200, 191);
    opacity: 0.85;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    border-radius: 10px;
    box-shadow: 2px 3px 15px black;
    padding-bottom: 15px;
}

.signUpFocus{
    animation: rotate;
    animation-duration: 1s;
}

.logInFocus{
    animation: rotate;
    animation-duration: 1s;
}

@keyframes rotate {
    0% {
        
        transform: rotateY(180deg);

    } 100% {
        transform: rotateY(0deg);
    }
}

.formContainer > h1 {
    margin-top: 30px;
    font-size: 70px;
    font-family: 'Creepster', cursive;
    margin-bottom: 15px;
}

.label {
    color: rgb(0, 0, 0);
    font-size: 25px;
}

.inputContainer {
    width: 70%;
    height: 115px;
}

.input {
    height: 35px;
    width: 300px;
    border-radius: 7px;
    border-color: rgb(45, 187, 192);
    padding-left: 10px;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
}

.errorContainer {
    width: 100%;
    height: 100%;
}

.error{
    color: red;
    margin: 0;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}

.hr {
    height: 1px;
    width: 355px;
    background-color: black;
    border: none;
}

.submitButton{
    margin-bottom: 10px;
    background-color: rgb(230, 244, 83);
    height: 43px;
    width: 200px;
    border-radius: 8px;
    border: none;
    font-size: 17px;
    color: black;
    box-shadow: 2px 3px 1px black;
}

.submitButton:hover{
    background-color: yellow;
    transition: 0.2s;
    opacity: 1;
}